import './WestForm.scss'

function WestForm() {
    return (
        <form className='termination'>
            <div className='west-form'>
                <input className="west-form__input form-input" type="text" name="name" placeholder=" '" required/>
                <input className="west-form__input form-input" type="tel" name="phone" maxLength="13" placeholder=" " required/>

                <select className='west-form__input form-input select'>
                    <option value=''> </option>
                    <option value=''></option>
                    <option value=''></option>
                    <option value=' '> </option>
                </select>

                <select className='west-form__input form-input select'>
                    <option value='' selected disabled>,    </option>
                    <option value='Viber'>Viber</option>
                    <option value='Telegram'>Telegram</option>
                    <option value='Wats App'>Wats App</option>
                </select>

                <button className="west-form__button main-button"></button>
            </div>
        </form>
    )
}

export default WestForm